    text-align: center;\n}\n\n.divider {\n    display: flex;\n    justify-content: center;\n    margin-top: -10px;\n}\n\nhr {\n    border: none;\n    border-top: 2px solid var(--lightgrey-clr);\n    width: 15%;\n}\n\n.sidebar h2 {\n    color: var(--dimmed);\n    font-size: 0.8rem;\n    font-weight: normal;\n    margin: 0 0 5px 20px;\n}\n\n.add-project {\n    color: var(--primary-clr);\n}\n\n\n.link.selected {\n    background-color: var(--lighter-primary-clr);\n    color: var(--primary-clr);\n    font-weight: bold;\n}\n\n/* header */\nheader {\n    padding: 30px 0;\n    width: 90%;\n    margin: 0 auto;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    gap: 10px;\n}\n\nheader .left {\n    color: var(--primary-clr);\n    display: flex;\n    align-items: center;\n    gap: 5px;\n    margin-left: 6px;\n    flex: 1;\n}\n\n.page-icon {\n    height: 35px;\n    display: block;\n}\n\n.page-icon.hidden {\n    display: none;\n}\n\n#page-title {\n    flex: 1;\n    color: var(--primary-clr);\n    font-size: 1.5rem;\n    font-weight: 500;\n    background-color: transparent;\n    border: none;\n    padding: 10px;\n    width: 100%;\n}\n\n#page-title:focus {\n    color: var(--black-clr);\n    border: 1px solid var(--dimmed);\n}\n\n.add-task {\n    display: flex;\n    justify-content: flex-end;\n    color: var(--dimmed);\n    transition: color .25s;\n}\n\n.add-task:hover {\n    color: var(--primary-clr);\n}\n\n.add-task a {\n    gap: 15px;\n}\n\n\n/* main */\nmain>.container {\n    width: 90%;\n    margin: 0 auto;\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n.task {\n    min-height: 70px;\n    background-color: white;\n    border-radius: 10px;\n    padding: 15px;\n    box-shadow: 1px 2px 8px -3px var(--dimmed);\n    display: flex;\n    align-items: center;\n    gap: 20px;\n    cursor: pointer;\n    transition: all .2s;\n}\n\n.task:hover {\n    background-color: var(--lightest-primary-clr);\n    filter: brightness(.99) saturate(1.2)\n}\n\n.checkbox-container {\n    display: block;\n    position: relative;\n    cursor: pointer;\n}\n\n.checkbox {\n    background-color: white;\n    border-radius: 50%;\n    vertical-align: middle;\n    border: 1px solid var(--primary-clr);\n    appearance: none;\n    -webkit-appearance: none;\n    cursor: pointer;\n    transition: background-color .15s;\n}\n\n.checkbox:hover {\n    background-color: var(--lighter-primary-clr);\n}\n\n.checkbox:checked {\n    background-color: var(--primary-clr);\n    appearance: auto;\n    clip-path: circle(50% at 50% 50%);\n}\n\n.task .checkbox {\n    width: 15px;\n    height: 15px;\n}\n\n.task .task-details {\n    display: flex;\n    flex-direction: column;\n    gap: 2px;\n    flex: 1;\n}\n\n.task-attributes {\n    color: var(--dimmed);\n    font-size: .8rem;\n    display: flex;\n    align-items: center;\n    gap: 10px;\n}\n\n.task-attributes li {\n    display: flex;\n    align-items: center;\n}\n\n.task-attributes figure {\n    margin: 0 5px 0 1px;\n}\n\n.task-attributes li:not(:first-child)::before {\n    content: \"\";\n    width: 0.25rem;\n    height: 0.25rem;\n    border-radius: 50%;\n    background-color: var(--dimmed);\n    margin-right: 0.5rem;\n}\n\n.description {\n    max-width: 20vw;\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n.today {\n    color: var(--primary-clr)\n}\n\n.overdue {\n    color: var(--highlight-clr);\n}\n\n/* DELETING BUTTON */\n.delete-icon-btn {\n    visibility: hidden;\n    opacity: 0;\n    mask-image: url(\"../icons/trash-x-outline.svg\");\n    mask-repeat: no-repeat;\n    background-color: var(--dimmed);\n    padding: 10px;\n    transition: filter .15s, visibility 0s, opacity .15s linear;\n}\n\n.task .delete-icon-btn {\n    height: 23px;\n    width: 40px;\n}\n\n.link .delete-icon-btn {\n    height: 20px;\n    width: 30px;\n}\n\n.delete-icon-btn:hover {\n    filter: brightness(.6);\n}\n\n.delete-icon-btn:active {\n    mask-image: url(\"../icons/trash-x-filled.svg\");\n}\n\n.task:hover .delete-icon-btn,\n.link:hover .delete-icon-btn {\n    visibility: visible;\n    opacity: 1;\n}\n\n\n/* urgent task */\n.task.urgent {\n    border-left: 10px solid var(--highlight-clr);\n}\n\n.task.urgent .checkbox-container {\n    margin-left: -10px;\n}\n\n.task.urgent .urgency {\n    color: var(--highlight-clr);\n    font-weight: bold;\n}\n\n\n/* completed task */\n.task.completed {\n    background-color: var(--lightest-primary-clr);\n    color: var(--dimmed);\n    border-color: transparent;\n}\n\n.task.completed figure,\n.task.completed p {\n    color: var(--dimmed);\n    font-weight: normal;\n}\n\n.task.completed .task-name {\n    text-decoration: line-through;\n}\n\n/* dialogs */\n/* todo: add custom error message for required fields not being filled in */\n\n::backdrop {\n    background-color: gray;\n    opacity: .7;\n}\n\ndialog {\n    margin: auto;\n    border: none;\n    border-radius: 10px;\n    padding: 30px 25px;\n    width: 30vw;\n    background-color: var(--lightest-primary-clr)\n}\n\ndialog form {\n    display: flex;\n    flex-direction: column;\n    gap: 25px;\n}\n\ninput,\ntextarea,\nselect {\n    width: 100%;\n    padding: 15px;\n    border-radius: 10px;\n    border: 2px solid var(--lighter-primary-clr);\n}\n\nselect {\n    padding-left: 10px;\n}\n\ninput:focus,\ntextarea:focus,\nselect:focus {\n    border: 2px solid var(--primary-clr);\n    box-shadow: 4px 4px 6px -1px rgba(0, 0, 0, 0.1);\n}\n\ndialog .top {\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n#new-project-name,\n#new-task-name,\n#task-name-in-modal,\nh2 {\n    font-size: 1.1rem;\n}\n\n#new-project-name.error {\n    border: 2px solid var(--warning-clr);\n}\n\n.error-message {\n    margin-left: 10px;\n    color: var(--warning-clr);\n    display: none;\n}\n\n.error-message.error {\n    display: block;\n}\n\n.btn-group {\n    display: flex;\n    justify-content: end;\n    gap: 15px;\n    margin-top: 15px;\n}\n\ndialog button {\n    padding: 8px 25px;\n    border-radius: 10px;\n    width: 100px;\n    transition: filter .18s;\n}\n\ndialog button:hover {\n    filter: brightness(1.05);\n}\n\ndialog button:active {\n    filter: brightness(.98);\n    transform: scale(.99);\n}\n\n.confirm-btn {\n    color: white;\n    background-color: var(--highlight-clr);\n}\n\n.cancel-btn {\n    color: #595959;\n    background-color: #dedede;\n}\n\ndialog.is-urgent {\n    border-left: 10px solid var(--highlight-clr);\n}\n\n.new-task-modal form {\n    gap: 15px;\n}\n\n.task-name-input-wrapper {\n    position: relative;\n    display: inline-block;\n    overflow: hidden;\n}\n\n.toggle-urgent-btn {\n    mask-image: url(\"../icons/alert-circle-outline.svg\");\n    background-color: var(--highlight-clr);\n    mask-repeat: no-repeat;\n    position: absolute;\n    top: 12px;\n    right: -50px;\n    height: 50%;\n\n}\n\n.is-urgent .toggle-urgent-btn {\n    mask-image: url(\"../icons/alert-circle-filled.svg\")\n}\n\n.input-container {\n    display: flex;\n    gap: 15px;\n}\n\n/* modal for details of tasks */\n.task-details-modal form {\n    flex-direction: row;\n    width: 100%;\n}\n\n.task-details-modal .checkbox {\n    width: 20px;\n    height: 20px;\n    border-width: 2px;\n    margin: 15px -10px 0 -5px;\n}\n\n.task-details-modal .task-details-fields {\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n    width: 100%;\n}\n\n/* modal for deletion confirmation */\n.delete-modal form {\n    gap: 15px;\n}\n\n.delete-modal h2 {\n    margin-bottom: 5px;\n}\n\n.delete-btn {\n    background-color: var(--warning-clr);\n    color: white;\n    width: fit-content;\n}"],"sourceRoot":""}]);
